"use client"

import { useState, useEffect } from 'react';
import Header from "@/components/Header";
import NoticeBoard from "@/components/NoticeBoard";
import CreateNoticeModal from "@/components/CreateNoticeModal";
import PasswordCheckModal from "@/components/PasswordCheckModal";
import { Notice } from '@/generated/prisma';

interface Inquiry {
  id: number;
  name: string;
  company: string;
  title: string;
  email: string;
  message: string;
  createdAt: string;
  updatedAt: string;
}

export default function AdminPage() {
  const [activeTab, setActiveTab] = useState<'inquiries' | 'notices'>('inquiries');
  const [inquiries, setInquiries] = useState<Inquiry[]>([]);
  const [notices, setNotices] = useState<Notice[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [isCreateModalOpen, setCreateModalOpen] = useState(false);
  const [isPasswordModalOpen, setPasswordModalOpen] = useState(false);

  useEffect(() => {
    fetchInquiries();
    fetchNotices();
  }, []);

  const fetchInquiries = async () => {
    try {
      const response = await fetch('/api/inquiries');
      if (response.ok) {
        const data = await response.json();
        setInquiries(data);
      } else {
        setError('문의 데이터를 불러오는 중 오류가 발생했습니다.');
      }
    } catch (error) {
      setError('문의 데이터를 불러오는 중 오류가 발생했습니다.');
    } finally {
      setLoading(false);
    }
  };

  const fetchNotices = async () => {
    try {
      const response = await fetch('/api/notices');
      if (response.ok) {
        const data = await response.json();
        setNotices(data);
      } else {
        console.error('공지사항 데이터를 불러오는 중 오류가 발생했습니다.');
      }
    } catch (error) {
      console.error('공지사항 데이터를 불러오는 중 오류가 발생했습니다.');
    }
  };

  const handleOpenCreateModal = () => {
    setPasswordModalOpen(false);
    setCreateModalOpen(true);
  };

  const handleCloseCreateModal = () => {
    setCreateModalOpen(false);
    fetchNotices(); // 공지사항 목록 새로고침
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleString('ko-KR');
  };

  if (loading) {
    return (
      <div className="min-h-screen flex flex-col">
        <Header />
        <main className="flex-grow container mx-auto px-4 py-16">
          <div className="max-w-6xl mx-auto">
            <div className="text-center">로딩 중...</div>
          </div>
        </main>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex flex-col">
        <Header />
        <main className="flex-grow container mx-auto px-4 py-16">
          <div className="max-w-6xl mx-auto">
            <div className="text-center text-red-600">{error}</div>
          </div>
        </main>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex flex-col">
      <Header />
      <main className="flex-grow container mx-auto px-4 py-16">
        <div className="max-w-6xl mx-auto">
          <h1 className="text-3xl font-bold mb-8">관리자 페이지</h1>
          
          {/* 탭 메뉴 */}
          <div className="flex space-x-4 mb-8">
            <button
              onClick={() => setActiveTab('inquiries')}
              className={`px-4 py-2 rounded-lg font-semibold transition-colors ${
                activeTab === 'inquiries'
                  ? 'bg-[#04AAAB] text-white'
                  : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
              }`}
            >
              문의 관리
            </button>
            <button
              onClick={() => setActiveTab('notices')}
              className={`px-4 py-2 rounded-lg font-semibold transition-colors ${
                activeTab === 'notices'
                  ? 'bg-[#04AAAB] text-white'
                  : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
              }`}
            >
              공지사항 관리
            </button>
          </div>

          {/* 문의 관리 탭 */}
          {activeTab === 'inquiries' && (
            <div className="bg-white rounded-lg shadow-md p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-semibold">문의 목록</h2>
                <span className="text-gray-600">총 {inquiries.length}건</span>
              </div>

              {inquiries.length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                  아직 문의가 없습니다.
                </div>
              ) : (
                <div className="space-y-4">
                  {inquiries.map((inquiry) => (
                    <div key={inquiry.id} className="border rounded-lg p-4 hover:shadow-md transition-shadow">
                      <div className="grid md:grid-cols-2 gap-4 mb-4">
                        <div>
                          <span className="font-semibold text-gray-700">이름:</span>
                          <span className="ml-2">{inquiry.name}</span>
                        </div>
                        <div>
                          <span className="font-semibold text-gray-700">회사:</span>
                          <span className="ml-2">{inquiry.company}</span>
                        </div>
                        <div>
                          <span className="font-semibold text-gray-700">직책:</span>
                          <span className="ml-2">{inquiry.title}</span>
                        </div>
                        <div>
                          <span className="font-semibold text-gray-700">이메일:</span>
                          <span className="ml-2">{inquiry.email}</span>
                        </div>
                      </div>
                      
                      <div className="mb-4">
                        <span className="font-semibold text-gray-700">문의 내용:</span>
                        <div className="mt-2 p-3 bg-gray-50 rounded-md">
                          <pre className="whitespace-pre-wrap text-sm">{inquiry.message}</pre>
                        </div>
                      </div>
                      
                      <div className="text-sm text-gray-500">
                        접수일: {formatDate(inquiry.createdAt)}
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}

          {/* 공지사항 관리 탭 */}
          {activeTab === 'notices' && (
            <div className="bg-white rounded-lg shadow-md p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-semibold">공지사항 관리</h2>
                <button
                  onClick={() => setPasswordModalOpen(true)}
                  className="bg-[#04AAAB] text-white font-semibold px-4 py-2 rounded-md hover:bg-[#038A8A] transition-colors"
                >
                  공지사항 작성
                </button>
              </div>
              
              <NoticeBoard notices={notices} />
            </div>
          )}
        </div>
      </main>

      {/* 모달들 */}
      <PasswordCheckModal
        isOpen={isPasswordModalOpen}
        onClose={() => setPasswordModalOpen(false)}
        onSuccess={handleOpenCreateModal}
      />
      <CreateNoticeModal
        isOpen={isCreateModalOpen}
        onClose={handleCloseCreateModal}
      />
    </div>
  );
} 